#app {
  height: 100vh;
}
.settings {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 240px;
  padding: 1rem;
  border-right: 1px solid #f0f0f0;
  overflow: auto;
  -webkit-transition: opacity 200ms;
  transition: opacity 200ms;
}
