}
.fontlist {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 16rem;
  margin-left: 16rem;
  margin-bottom: 0;
  padding: 1.2rem 1rem;
  overflow: auto;
  border-right: 1px solid #f0f0f0;
  font-size: 1.1rem;
  -webkit-transition: opacity 100ms;
  transition: opacity 100ms;
}
.fontlist li {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    line-height: 2;
}
.fontlist li a {
      width: 100%;
      font: 400 1.2rem "Asap Condensed";
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
}
.fontlist li a.router-link-active {
        color: #0366d6 !important;
        cursor: default;
}
.fontlist li a:hover, .fontlist li a:focus {
