}
.fontlist li .favorite svg {
      height: 15px;
      width: 15px;
      margin-top: 2px;
      -webkit-transition: all 200ms;
      transition: all 200ms;
}
.fontlist li .favorite--yes {
      fill: #dd3030;
}
.fontlist li .favorite--no {
      fill: #dfdfdf;
      fill-opacity: .5;
}
.fontlist--empty li {
    margin-left: -.3rem;
}
.content {
  height: 100vh;
  padding-right: 3rem;
  -webkit-transition: opacity 200ms, -webkit-transform 500ms;
  transition: opacity 200ms, -webkit-transform 500ms;
  transition: opacity 200ms, transform 500ms;
  transition: opacity 200ms, transform 500ms, -webkit-transform 500ms;
}
