.form-control {
  font: 400 1.1rem "Source Sans Pro";
  color: #333;
  width: 100%;
  height: 45px;
  padding: 1rem;
  padding: 6px 8px;
  background-color: #fff;
  border: 1px solid #d1d5da;
  border-radius: 3px;
  -webkit-box-shadow: inset 0 1px 2px rgba(27, 31, 35, 0.075);
          box-shadow: inset 0 1px 2px rgba(27, 31, 35, 0.075);
}
.form-control:disabled {
    opacity: .5;
}
.form-control ~ .field-title {
    opacity: .5 !important;
}
label {
  cursor: pointer;
}
select {
  text-transform: capitalize;
  color: #495057;
}
.checkbox {
  margin-top: .75rem;
}
.checkbox label {
    font-weight: 400;
    font-size: 1.1rem;
    display: block;
    position: relative;
    padding-left: 2.3rem;
    cursor: pointer;
}
.checkbox label:first-letter {
      text-transform: uppercase;
}
.checkbox span {
    -webkit-transition: opacity 250ms;
    transition: opacity 250ms;
}
.checkbox input {
    position: absolute;
    left: 0;
    z-index: -1;
    opacity: 0;
    visibility: hidden;
}
.checkbox input:disabled ~ span.label {
      cursor: default;
      opacity: .5;
}
.checkbox input:disabled ~ span.label .sublabel {
        display: inline-block;
        opacity: .5;
}
.checkbox input:disabled:checked ~ .checkbox__indicator {
      background: rgba(3, 102, 214, 0.5) !important;
}
.checkbox input:checked ~ .checkbox__indicator {
      background: -webkit-gradient(linear, left top, right bottom, from(#0366d6), to(#0372ef));
      background: linear-gradient(to bottom right, #0366d6, #0372ef);
}
.checkbox input:checked ~ .checkbox__indicator:after {
        display: block;
}
.checkbox input:not(:checked) ~ .label {
      opacity: .6;
}
.checkbox__indicator {
    position: absolute;
    top: 0px;
    left: 0;
    height: 20px;
    width: 20px;
    background: -webkit-gradient(linear, left top, right bottom, from(#dedede), to(#eee));
    background: linear-gradient(to bottom right, #dedede, #eee);
    border-radius: 3px;
}
.checkbox__indicator:after {
      content: '';
      position: absolute;
      display: none;
      left: 8px;
      top: 4px;
      width: 5px;
      height: 10px;
      border: solid #fff;
      border-width: 0 2px 2px 0;
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
}
